import React, { Component } from 'react';
import axios from 'axios';

import {Button, Card, CircularProgress, Icon, Paper, TextField} from '@material-ui/core';
import RefreshIcon from "@material-ui/icons/Refresh";
import FlagIcon from 'react-flag-kit/lib/FlagIcon';

const flagMap = {
  'en': 'GB',
  'ar': 'SA',
  'nl': 'NL',
  'el': 'GR',
  'iw': 'IL',
  'it': 'IT',
  'es': 'ES',
  'zh-cn': 'CN',
  'ko': 'KR',
  'de': 'DE',
  'no': 'NO',
  'fa': 'IR',
  'pl': 'PL',
  'pt': 'PT',
  'ru': 'RU',
  'sw': 'KE',
  'th': 'TH',
  'tl': 'PH',
  'tr': 'TR',
  'fr': 'FR',
  'hi': 'IN',
  'ja': 'JP'
}

export default class App extends Component {
  
  constructor(props) {
    super(props);
    
    this.state = {
      sentence: '',
      result: '',
      steps: [],
      isLoading: false
    }
  }
  
  render() {
    return (
      <div className="App">
      
        {this.renderInput()}
        
        {this.renderSteps()}
        
        {this.renderResult()}
      
      </div>
    );
  }
  
  renderInput() {
    return <Paper
      square={true}
      style={styles.header}
    >
      <TextField
        style={styles.sentence}
        rowsMax={3}
        multiline={true}
        placeholder={'Input Sentence'}
        value={this.state.sentence}
        onChange={this.handleChange}
      />
      
      <Button variant="fab" color="primary"
        style={styles.button}
        onClick={this.onSubmit}
      >
        <RefreshIcon />
      </Button>
    </Paper>
  }
  
  renderSteps() {
    
    const { steps, isLoading } = this.state;
    
    let cards = steps.map((step, index) => {
      return <Card
        square={true}
        style={styles.card}
        key={index}
      >
        <div style={{display: 'flex', flexDirection: 'row', alignItems: 'center', }}>
          <div style={{width: 30, padding: 10}}><FlagIcon code={flagMap[step.language]} size={32} /></div>
          <p>{step.text}</p>
        </div>
      </Card>
    })
    
    return isLoading?
      <div style={styles.spinner}>
        <CircularProgress size={100}/>
      </div> :
      
      <div style={{paddingBottom: 110, paddingTop: 100}}>
        {cards}
      </div>
  }
  
  renderResult(){
    return <Paper
      square={true}
      style={styles.footer}
    >
      <p>{this.state.result}</p>
    </Paper>
  }
  
  handleChange = (event) => {
    this.setState({sentence: event.target.value});
  }
  
  onSubmit = (event) => {
    
    this.setState({isLoading: true, result: '', steps: []})
    
    if (this.state.sentence.length > 1) {
      axios.post('https://babelrang.herokuapp.com', {
        sentence: this.state.sentence
      })
      .then(response => {
        const {result, steps } = response.data;
        this.setState({result, steps, isLoading: false})
      })
    }
  }
}

const styles = {
  header: {
    padding: 10,
    position: 'fixed',
    top: 0,
    width: '100%',
    height: 80,
    background: '#2196F3'
  },
  footer: {
    padding: 10,
    position: 'fixed',
    bottom: 0,
    width: '100%',
    height: 80,
    background: '#2196F3'
  },
  spinner: {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)'
  },
  card: {
    marginTop: 10,
    marginLeft: 10,
    marginRight: 10,
    maxHeight: 80,
    background: '#E1F5FE'
  },
  button: {
    position: 'absolute',
    right: 30,
    top: 70
  },
  sentence: {
    width: '90%',
    
  }
}



